import { BASE_IMAGE_URL,getChuOrder,confirmShipping } from '@/utils/api'
import uniPopup from '@/components/uni-popup.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		uniPopup
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
			loadingType: 0,
			page: 1,
			typeList: ['待发货', '待收货', '已完成'],
			curType: 0,
			type:'',
			companyCode: ''
		}
	},
	methods: {
		changeType(index){
			this.curType = index;
			this.page = 1;
			this.list = [];
			this.getList();
		},
		async getList(){
			let res = await getChuOrder(this.curType, this.page);
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		},
		
		async confirmShipping(index){
			let res = await confirmShipping(this.list[index].o_id, this.list[index].o_express_name, this.list[index].o_express_no);
			if(res.code == 1000){
				
			}
		}
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(){
		
		this.getList();
	}
	
}
